@import url('https://fonts.googleapis.com/css?family=Roboto:100');
html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

#logo {
  float: left;
  margin-top: 10px;
}

#title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding-left: 10%;
  color: #48486e;
  font-family: Roboto;
  font-weight: 900;
  -webkit-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
     transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in-out;
       -o-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}

#divLogo {
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 180px;
}

#titleHalf {
  color: #34b3d3;
  font-family: Roboto;
  font-weight: 900;
}

#navBar {
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: right;
  padding-right: 10%;
  padding-top: 5px;
  min-width: 205px;
}

.nav {
  display: inline-block;
  color: #48486e;
  font-weight: bold;
  padding: 0px 5px;
}

#accLink {
  text-decoration: none;
  color: #48486e;
}

#accLink:hover {
  color: #34b3d3;
}

#icon {
  color: black;
  padding: 10px;
  margin: 15px 10px;
  text-decoration: none;
  display: none;
  outline: none;
}

#icon:hover {
  color: lightgray;
  outline: none;
}

#icon:focus, #icon:active, #icon:visited {
  outline: none;
}

#about {
  padding-top: 40px;
  padding-bottom: 60px;
  color: white;
  text-align: center;
  background-color: #48486e;
  font-family: Roboto;
  font-weight: 100;
  font-size: 20px;
}

#bikeImg {
  max-width: 100%;
}

.button {
  padding: 10px;
  margin-bottom: 10px;
  font-family: Roboto;
  -webkit-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
     transition-duration: 0.5s;
}

#btnDetails {
  margin-top: 60px;
}

#parcelBtn {
  background-color: #48486e;
  border: 2px #34b3d3 solid;
  border-right-width: 1px;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  width: 280px;
}

#parcelBtn:hover {
  background-color: #34b3d3;
  border: 2px #34b3d3 solid;
  color: white;
}

#bikeBtn {
  background-color: #48486e;
  border: 2px #34b3d3 solid;
  border-collapse: collapse;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  width: 280px;
}

#bikeBtn:hover {
  background-color: #34b3d3;
  border: 2px #34b3d3 solid;
  color: white;
}

.caret {
  position: relative;
  left: 30px;
}

#priceBtn {
  background-color: #f6416c;
  border: 2px #f6416c solid;
  border-left-width: 1px;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  width: 280px;
}

#priceBtn:hover {
  background-color: white;
  border: 2px white solid;
  color: #f6416c;
}

#procedure {
  padding-top: 40px;
  padding-left: 15%;
  padding-right: 15%;
  text-align: center;
  font-family: Roboto;
  font-size: 20px;
}

#procedureSteps {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.steps {
  -ms-flex: 1 1;
      flex: 1 1;
}

.underline {
  border-bottom: 3px #f6416c solid;
}

#stepDesc {
  padding-top: -10px;
}

.stepImg {
  padding-bottom: 0px;
}

#btn {
  margin-top: 20px;
}

#book {
  margin: 0px 5px;
  background-color: #f6416c;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  width: 200px;
}

#book:hover {
  background-color: white;
  border: 2px #f6416c solid;
  color: #f6416c;
}

#contact {
  margin: 5px 5px;
  background-color: white;
  border: 2px #34b3d3 solid;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  width: 200px;
}

#contact:hover {
  background-color: #34b3d3;
  border: 2px #34b3d3 solid;
  color: white;
}

#foot {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: 50px;
  color: white;
  text-align: center;
  background-color: #48486e;
  font-family: Roboto;
  font-weight: 100;
  font-size: 20px;
}

.footPart {
  -ms-flex: 1 1;
      flex: 1 1;
  min-width: 130px;
  padding-left: 10%;
  width: 12%;
  display: inline-block;
  text-align: left;
}

.footPart:nth-of-type(4) {
  -ms-flex: 5 1;
      flex: 5 1;
  min-width: 130px;
  padding-bottom: 20px;
}

li {
  padding-bottom: 10px;
}

#linksList {
  margin-top: -10px;
  padding-left: 0px;
  list-style: none;
  font-size: 12px;
  text-align: left;
}

.footLinks {
  color: white;
  text-decoration: none;
  font-family: Roboto;
}

.footLinks:hover {
  color: lightgray;
}

#social {
  padding-top: 40px;
  text-align: center;
}

#fb {
  position: relative;
  right: 10px;
}

#insta {
  position: relative;
  left: 10px;
}

#openNav {
  display: block;
  text-align: right;
  padding-right: 10%;
  padding-top: 5px;
  min-width: 205px;
  -webkit-transition-duration: 1s;
  -o-transition-duration: 1s;
     transition-duration: 1s;
}

@media only screen and (max-width: 516px) {
  #icon {
    display: inline-block;
  }
  #navBar {
    display: none;
  }
}



